import { Row, Col } from "antd";
import { withTranslation, TFunction } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { Button } from "../../common/Button";
import { AeroAdminSection, Content, ContentWrapper } from './styles'

interface AeroAdminProps {
  title: string;
  content: string;
  button: string;
  t: TFunction;
}


const AeroAdminProps = ({ title, content, button, t }: AeroAdminProps) => {
  const scrollTo = (id: string) => {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
  };
  return (
    <AeroAdminSection>
      <Slide direction="up" triggerOnce>
        <Row justify="center" align="middle">
          <ContentWrapper>
            <Col lg={24} md={24} sm={24} xs={24}>
              <h6>{t(title)}</h6>
              <Content>{t(content)}</Content>
              {button && (
                <Button color="primary" href="https://ulm.aeroadmin.com/AeroAdmin.exe"> Download AeroAdmin </Button>

              )}
            </Col>
          </ContentWrapper>
        </Row>
      </Slide>
    </AeroAdminSection>
  );
};

export default withTranslation()(AeroAdminProps);

